"use client";

import { useEffect, useState } from "react";
import { useAudio } from "@/context/AudioContext";
import { Heart, Music, Film, Tv, Trash2, Play } from "lucide-react";

type Track = {
  id: number;
  title: string;
  artist: string;
  album?: string;
  url: string;
  cover?: string;
};

type Favorite = {
  id: number;
  type: string;
  trackId?: number;
  movieId?: number;
  seriesId?: number;
  track?: Track;
  createdAt: string;
};

export default function FavoritesPage() {
  const [favorites, setFavorites] = useState<Favorite[]>([]);
  const [filter, setFilter] = useState<string>("all");
  const { playTrack } = useAudio();

  useEffect(() => {
    fetchFavorites();
  }, []);

  const fetchFavorites = async () => {
    try {
      const res = await fetch("/api/favorites");
      const data = await res.json();
      setFavorites(Array.isArray(data) ? data : []);
    } catch (error) {
      console.error("Erreur chargement favoris:", error);
      setFavorites([]);
    }
  };

  const removeFavorite = async (id: number) => {
    try {
      await fetch(`/api/favorites?id=${id}`, { method: "DELETE" });
      fetchFavorites();
    } catch (error) {
      console.error("Erreur suppression favori:", error);
    }
  };

  const filteredFavorites = filter === "all" 
    ? favorites 
    : favorites.filter(f => f.type === filter);

  return (
    <div className="max-w-7xl mx-auto">
      <h1 className="text-3xl font-bold mb-6 flex items-center gap-3">
        <Heart className="text-red-500" size={32} />
        Mes Favoris
      </h1>

      {/* Filtres */}
      <div className="flex gap-2 mb-6">
        <button
          onClick={() => setFilter("all")}
          className={`px-4 py-2 rounded-lg ${
            filter === "all" 
              ? "bg-blue-600 text-white" 
              : "bg-gray-200 dark:bg-gray-700"
          }`}
        >
          Tous
        </button>
        <button
          onClick={() => setFilter("track")}
          className={`flex items-center gap-2 px-4 py-2 rounded-lg ${
            filter === "track" 
              ? "bg-blue-600 text-white" 
              : "bg-gray-200 dark:bg-gray-700"
          }`}
        >
          <Music size={18} />
          Musiques
        </button>
        <button
          onClick={() => setFilter("movie")}
          className={`flex items-center gap-2 px-4 py-2 rounded-lg ${
            filter === "movie" 
              ? "bg-blue-600 text-white" 
              : "bg-gray-200 dark:bg-gray-700"
          }`}
        >
          <Film size={18} />
          Films
        </button>
        <button
          onClick={() => setFilter("series")}
          className={`flex items-center gap-2 px-4 py-2 rounded-lg ${
            filter === "series" 
              ? "bg-blue-600 text-white" 
              : "bg-gray-200 dark:bg-gray-700"
          }`}
        >
          <Tv size={18} />
          Séries
        </button>
      </div>

      {/* Liste des favoris */}
      {filteredFavorites.length === 0 ? (
        <div className="text-center py-12 text-gray-400">
          <Heart size={64} className="mx-auto mb-4 opacity-50" />
          <p>Aucun favori pour le moment</p>
        </div>
      ) : (
        <div className="space-y-3">
          {filteredFavorites.map((fav) => (
            <div
              key={fav.id}
              className="bg-gray-100 dark:bg-gray-800 rounded-lg p-4 flex items-center gap-4 hover:bg-gray-200 dark:hover:bg-gray-700 transition-colors"
            >
              {/* Icône de type */}
              <div className="text-gray-400">
                {fav.type === "track" && <Music size={24} />}
                {fav.type === "movie" && <Film size={24} />}
                {fav.type === "series" && <Tv size={24} />}
              </div>

              {/* Informations */}
              <div className="flex-1">
                {fav.type === "track" && fav.track && (
                  <>
                    <div className="font-semibold">{fav.track.title}</div>
                    <div className="text-sm text-gray-500 dark:text-gray-400">
                      {fav.track.artist}
                      {fav.track.album && ` • ${fav.track.album}`}
                    </div>
                  </>
                )}
                {fav.type === "movie" && (
                  <div className="font-semibold">Film ID: {fav.movieId}</div>
                )}
                {fav.type === "series" && (
                  <div className="font-semibold">Série ID: {fav.seriesId}</div>
                )}
              </div>

              {/* Actions */}
              <div className="flex gap-2">
                {fav.type === "track" && fav.track && (
                  <button
                    onClick={() => playTrack(fav.track!)}
                    className="p-2 bg-blue-600 hover:bg-blue-700 rounded-lg"
                    title="Lire"
                  >
                    <Play size={18} />
                  </button>
                )}
                <button
                  onClick={() => removeFavorite(fav.id)}
                  className="p-2 bg-red-500 hover:bg-red-600 rounded-lg"
                  title="Retirer des favoris"
                >
                  <Trash2 size={18} />
                </button>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
